import { UtilProvider } from './../util';
import { Events } from 'ionic-angular';
import { Injectable } from '@angular/core';
declare let RongIMLib: any;
declare let RongIMClient: any;
@Injectable()
export class RongcloudProvider {
  appkey = "cpj2xarlc11bn";

  /* 
  //‰∏ÄÂÆöË¶ÅÊ≥®ÊÑè‰º†ÈÄíÂèÇÊï∞ÁöÑÁ±ªÂûã
  PRIVATE ‰∏∫ÂçïËÅä„ÄÅ
  DISCUSSION ‰∏∫ËÆ®ËÆ∫ÁªÑ„ÄÅ
  GROUP ‰∏∫Áæ§ÁªÑ„ÄÅ
  CHATROOM ‰∏∫ËÅäÂ§©ÂÆ§„ÄÅ
  CUSTOMER_SERVICE ‰∏∫ÂÆ¢Êúç„ÄÅ
  SYSTEM ‰∏∫Á≥ªÁªüÊ∂àÊÅØ„ÄÅ
  APP_PUBLIC_SERVICE ‰∏∫Â∫îÁî®ÂÖ¨‰ºóË¥¶Âè∑ÔºàÂ∫îÁî®ÂÜÖÁßÅÊúâÔºâ„ÄÅ
  PUBLIC_SERVICE ‰∏∫ÂÖ¨‰ºóË¥¶Âè∑ (ÂÖ¨Êúâ)
  
  */
  constructor(public util: UtilProvider, private events: Events) {
    this.init();
  }

  /* ÂàùÂßãÂåñ */
  init() {
    RongIMLib.RongIMClient.init(this.appkey, null, {});
    this.setConnectionStatusListener();
    this.setOnReceiveMessageListener();
    this.connect();
  }

  /* ËÆæÁΩÆËøûÊé•Áä∂ÊÄÅÁõëÂê¨,Âú®initÂêéÔºåconnectÂâç */
  setConnectionStatusListener() {
    RongIMClient.setConnectionStatusListener({
      onChanged: function (status) {
        console.log(status)
        switch (status) {
          case 0:
            console.log("CONNECTEDËøûÊé•ÊàêÂäü");
            break;
          case 1:
            console.log("CONNECTINGËøûÊé•‰∏≠");
            break;
          case 2:
            console.log("DISCONNECTEDÊñ≠ÂºÄËøûÊé•");
            break;
          case 6:
            console.log("KICKED_OFFLINE_BY_OTHER_CLIENTÁî®Êà∑Ë¥¶Êà∑Âú®ÂÖ∂‰ªñËÆæÂ§áÁôªÂΩïÔºåÊú¨Êú∫‰ºöË¢´Ë∏¢ÊéâÁ∫ø„ÄÇ");
            break;
          case -1:
            console.log("NETWORK_UNAVAILABLEÁΩëÁªú‰∏çÂèØÁî®");
            break;
        }
      }
    });
  }

  /* ËÆæÁΩÆÊ∂àÊÅØÁõëÂê¨ */
  setOnReceiveMessageListener() {
    let this_ = this;
    RongIMClient.setOnReceiveMessageListener({
      // Êé•Êî∂Âà∞ÁöÑÊ∂àÊÅØ
      onReceived: function (message) {
        this_.events.publish('newMessage', message);
      }
    });
  }


  /* ËøûÊé• */
  connect() {
    if (JSON.stringify(this.util.getItem("RongCloudToken")) == "{}") {
      this.util.post('/rongcloud/getToken', { uid: this.util.getItem("User").uid }).then((result: any) => {
        if (result.err == 0) {
          this.util.setItem("RongCloudToken", result.data.token);
          RongIMClient.connect(result.data.token, {
            onSuccess: function (userId) {
              console.log("ÈìæÊé•ÊàêÂäüÔºåÁî®Êà∑idÔºö" + userId);
            },
            onTokenIncorrect: function () {
              console.log('tokenÊó†Êïà');
            },
            onError: function (errorCode) {
              console.log("=============================================");
              console.log(errorCode);
            }
          });
        } else {
          this.util.showLoading("TokenËé∑ÂèñÂ§±Ë¥•");
        }
      }).catch((error) => {

      })
    } else {
      RongIMClient.connect(this.util.getItem("RongCloudToken"), {
        onSuccess: function (userId) {
          console.log("ÈìæÊé•ÊàêÂäüÔºåÁî®Êà∑idÔºö" + userId);
        },
        onTokenIncorrect: function () {
          console.log('tokenÊó†Êïà');
        },
        onError: function (errorCode) {
          console.log("=============================================");
          console.log(errorCode);
        }
      });
    }



  }

  /* Ëé∑Âèñ‰ºöËØùÂàóË°® 
  
   @params 
   conversationTypes = null;  //ÂÖ∑‰ΩìÊ†ºÂºèËÆæÁΩÆÈúÄË¶ÅË°•ÂÖÖ
   limit = 1000; //Ëé∑Âèñ‰ºöËØùÁöÑÊï∞ÈáèÔºå‰∏ç‰º†Êàñ‰º†null‰∏∫ÂÖ®ÈÉ®ÔºåÊöÇ‰∏çÊîØÊåÅÂàÜÈ°µ
  */
  getConversationList() {
    return new Promise((resolve, reject) => {
      let conversationTypes = null;  //ÂÖ∑‰ΩìÊ†ºÂºèËÆæÁΩÆÈúÄË¶ÅË°•ÂÖÖ
      let limit = null; //Ëé∑Âèñ‰ºöËØùÁöÑÊï∞ÈáèÔºå‰∏ç‰º†Êàñ‰º†null‰∏∫ÂÖ®ÈÉ®ÔºåÊöÇ‰∏çÊîØÊåÅÂàÜÈ°µ
      RongIMClient.getInstance().getConversationList({
        onSuccess: function (list) {
          resolve(list)
        },
        onError: function (error) {
          resolve([]);
        }
      }, conversationTypes, limit);
    })
  }
  /* Ëé∑Âèñ‰ºöËØù */
  getConversation(data) {
    //ÈúÄÂú® getConversationList ÊñπÊ≥ïÊâßË°å‰πãÂêéÊâßË°åÔºåÂê¶ÂàôËøîÂõûnull
    RongIMClient.getInstance().getConversation(data.conversationType, data.targetId, {
      onSuccess: function (result) {
        console.log(result)
      },
      onError: function (error) {
        console.log(error)
      }
    });
  }
  /* ÁßªÈô§ÊüêÊù°‰ºöËØù */
  removeConversation(data) {
    return new Promise((resolve, reject) => {
      RongIMClient.getInstance().removeConversation(data.conversationType, data.targetId, {
        onSuccess: function (result) {
          resolve(1);
        },
        onError: function (error) {
          resolve(0);
        }
      });
    })
  }

  /* Ê∏ÖÈô§‰ºöËØùÂàóË°® */
  clearConversation() {
    RongIMClient.getInstance().clearConversations({
      onSuccess: function () {
        console.log("Ê∏ÖÈô§‰ºöËØùÊàêÂäü");
      },
      onError: function (error) {
        // error => Ê∏ÖÈô§‰ºöËØùÈîôËØØÁ†Å„ÄÇ 
        console.log("Ê∏ÖÈô§‰ºöËØùÂ§±Ë¥•");
      }
    });
  }

  /* Ê∏ÖÈô§Êú™ËØªÊï∞ */
  clearUnreadCount(data) {
    RongIMClient.getInstance().clearUnreadCount(data.conversationType, data.targetId, {
      onSuccess: function () {
        console.log("Ê∏ÖÈô§ÊàêÂäü");
      },
      onError: function (error) {
        console.log("Ê∏ÖÈô§Â§±Ë¥•");
      }
    });
  }

  /* Ëé∑ÂèñÂéÜÂè≤Ê∂àÊÅØ */
  getHistroyMessage(data) {
    /*
    Ê≥®ÊÑè‰∫ãÈ°πÔºö
      1Ôºö‰∏ÄÂÆö‰∏ÄÂÆö‰∏ÄÂÆöË¶ÅÂÖàÂºÄÈÄö ÂéÜÂè≤Ê∂àÊÅØ‰∫ëÂ≠òÂÇ® ÂäüËÉΩÔºåÊú¨ÊúçÂä°Êî∂Ë¥πÔºåÊµãËØïÁéØÂ¢ÉÂèØÂÖçË¥πÂºÄÈÄö
      2ÔºöÁôªÂΩïÂºÄÂèëËÄÖÂêéÂè∞ÂèØ‰ª•Áõ¥Êé•ÂºÄÂêØ https://developer.rongcloud.cn/app
      2ÔºötimestrapÁ¨¨‰∫åÊ¨°ÊãâÂèñÂøÖÈ°ª‰∏∫nullÊâçËÉΩÂÆûÁé∞Âæ™ÁéØÊãâÂèñ
    */
    return new Promise((resolve, reject) => {
      let count = 20;  // 2 <= count <= 20
      var timestrap = 0; //0, 1483950413013
      RongIMClient.getInstance().getHistoryMessages(data.conversationType, JSON.stringify(data.targetId), timestrap, count, {
        onSuccess: function (list, hasMsg) {
          //ÂèØÈÄöËøásortËÆ¢Âà∂ÂÖ∂‰ªñÈ°∫Â∫è
          list.sort(function (a, b) {
            return a.sentTime > b.sentTime;
          });
          resolve(list);
        },
        onError: function (error) {
          resolve([]);
        }
      });
    })
  }
  clearMessages(data) {
    console.log(data)
    return new Promise((resolve, reject) => {
      RongIMClient.getInstance().clearMessages(data.conversationType, JSON.stringify(data.targetId), {
        onSuccess: function (isClear) {
          if (isClear) {
            resolve(1);
          } else {
            resolve(0);
          }
        },
        onError: function () {
          resolve(0);
        }
      });
    })
  }

  /* ÂèëÈÄÅÊñáÊú¨Ê∂àÊÅØ 
  conversationType:number
  targetId:string
  */
  sendTextMessage(data) {
    return new Promise((resolve, reject) => {
      let pushData = "pushData" + Date.now();
      let content = {
        content: data.text,
        user: {
          "uid": data.uid,	//‰∏çÊîØÊåÅ‰∏≠ÊñáÂèäÁâπÊÆäÂ≠óÁ¨¶
          "name": data.nickname,
          "portrait": data.avatar
        },
        extra: JSON.stringify({ avatar: data.targetAvatar, nickname: data.targetNickname, uid: data.targetId })
      };
      let msg = new RongIMLib.TextMessage(content);
      RongIMClient.getInstance().sendMessage(data.conversationType, JSON.stringify(data.targetId), msg, {
        onSuccess: function (message) {
          resolve(message);
        },
        onError: function (errorCode, message) {
          resolve("");
        }
      }, false, pushData);
    })
  }

  /* ÂèëÈÄÅÂõæÁâáÊ∂àÊÅØ */
  sendImageMessage(data) {
    /*
    ÈúÄËá™Ë°åËß£ÂÜ≥Êñá‰ª∂‰∏ä‰º†  
    Áº©Áï•ÂõæÂøÖÈ°ªÊòØbase64Á†ÅÁöÑjpgÂõæÔºåËÄå‰∏î‰∏çÂ∏¶ÂâçÁºÄ"data:image/jpeg;base64,"Ôºå‰∏çÂæóË∂ÖËøá100K
    */
    return new Promise((resolve, reject) => {
      let content = {
        imageUri: data.imageUri,
        content: "",
        user: {
          "uid": data.uid,
          "name": data.nickname,
          "portrait": data.avatar
        },
        extra: JSON.stringify({ avatar: data.targetAvatar, nickname: data.targetNickname, uid: data.targetId })
      };
      this.util.url2Base64(data.text).then((result:any) => {
        content.content = result;
        let msg = new RongIMLib.ImageMessage(content);
        RongIMClient.getInstance().sendMessage(data.conversationType, JSON.stringify(data.targetId), msg, {
          onSuccess: function (message) {
            resolve(message);
          },
          onError: function (errorCode, message) {
            resolve("");
          }
        });
      }).catch(error => { });
    })
  }
}



